body{
    margin: 0;
    background-color: gray;
    font-family: sans-serif;
  }
  a {
    color: white;
    text-decoration: none;
  }
  
  .main-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    min-height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  h1 {
    color: white;
    font-size: 30em;
    font-family: sans-serif;
    text-shadow: 0 0 10px black;
    text-align: center;
    margin: 0;
  }
  
  .astronaut-list{
    color: white;
    font-size: 2em;
    padding: 10px;
    border-radius: 10px;
  }
  .astronaut-list-item{
    text-align: center;
    text-shadow: 0 0 10px black;
  }